.footer {
  background-color: black;
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.footer__content {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer__item,
.footer__copy-rights,
.footer__logo {
  color: #fff;
  padding: 1em;
}

.footer__logo {
  border: 1px solid rgba(255, 255, 255, 0.3);
}

.footer__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.5em;
  height: 130px;
  list-style: none;
}

.footer__subscription-field {
  display: flex;
  align-items: center;
  width: 100%;
}

.footer__subscription-field input {
  width: 70%;
}

.footer__subscription-field button {
  background-color: gray;
  color: #fff;
  width: 30%;
  border: none;
  outline: none;
  padding: 0.8em;
}

.footer__subscription-field * {
  border: none;
  outline: none;
  padding: 0.8em;
}

.footer__social-links {
  color: #fff;
  font-size: 2em;
  display: flex;
  width: 100%;
  justify-content: space-between;
}

a {
  text-decoration: none;
  list-style: none;
  color: #fff;
}

@media screen and (max-width: 768px) {
  .footer {
    height: 800px;
    text-align: center;
  }

  .footer__item {
    border-bottom: 1px solid rgba(255, 255, 255, 0.2);
    width: 100%;
    align-items: center;
     height: auto;
  }

  .footer__contact-info p {
    display: flex;
     gap: 10px;
  }

  .footer__content {
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 5em;
  }
}